.banner-sec {
    &[data-iseeditor="1"] {
        & .info {
            height: auto;
        }
    }

    margin-bottom: $componentmargin-btm;

    & .info {
        position: absolute;
        width: 100%;

        & .desc {
            width: 100%;
        }
    }

    & img {
        width: 100%;
    }
}

.videobanner-sec {
    & iframe{
        border:none;
    }
}