header {
    & nav {
        & button{
            float: right;
        }
        &.navbar {

            &[data-isEEditor="1"]{
                &.fixed-top{
                    position: relative!important;
                }
            }
            & .navbar-collapse {
                & ul {
                    & li {
                        &.dropdown-submenu {
                            position: relative;

                            & .dropdown-menu {
                                top: 0;
                                left: 100%;
                                margin-top: 0px;
                                margin-left: 0px;
                            }
                        }
                    }
                }
            }
        }
    }
}
