footer {
    & .footer-logo {
        display: flex;
        display: -webkit-flex;
        flex-wrap: wrap;
        -webkit-flex-wrap: wrap;
        width: 100%;
        padding-right: 15px;
        padding-left: 15px;

        & img {
            width: 200px;
        }
    }

    & .footer-menu {
        -webkit-box-flex: 0;
        -ms-flex: 0 0 100%;
        -webkit-flex: 0 0 100%;
        flex: 0 0 100%;
        max-width: 100%;
        display: flex;
        display: -webkit-flex;
        flex-wrap: wrap;
        -webkit-flex-wrap: wrap;
        padding-right: 15px;
        padding-left: 15px;

        & .footer-item {
            display: flex;
            display: -webkit-flex;
            flex-wrap: wrap;
            -webkit-flex-wrap: wrap;
            align-content: flex-start;
            -webkit-align-content: flex-start;
            -webkit-box-flex: 0;
            -ms-flex: 0 0 50%;
            -webkit-flex: 0 0 50%;
            flex: 0 0 50%;
            max-width: 50%;
            padding-right: 15px;

            &:only-child {
                -webkit-box-flex: 0;
                -ms-flex: 0 0 100%;
                flex: 0 0 100%;
                -webkit-flex: 0 0 100%;
                max-width: 100%;
            }

            &:first-child:nth-last-child(2) {
                -webkit-box-flex: 0;
                -ms-flex: 0 0 50%;
                flex: 0 0 50%;
                -webkit-flex: 0 0 50%;
                max-width: 50%;
            }

            &:first-child:nth-last-child(2) ~ .footer-item {
                -webkit-box-flex: 0;
                -ms-flex: 0 0 50%;
                flex: 0 0 50%;
                -webkit-flex: 0 0 50%;
                max-width: 50%;
            }
            /* three items */
            &:first-child:nth-last-child(3) {
                -webkit-box-flex: 0;
                -ms-flex: 0 0 33.3333%;
                flex: 0 0 33.3333%;
                -webkit-flex: 0 0 33.3333%;
                max-width: 33.3333%;
            }

            &:first-child:nth-last-child(3) ~ .footer-item {
                -webkit-box-flex: 0;
                -ms-flex: 0 0 33.3333%;
                flex: 0 0 33.3333%;
                -webkit-flex: 0 0 33.3333%;
                max-width: 33.3333%;
            }

            &:first-child:nth-last-child(4) {
                -webkit-box-flex: 0;
                -ms-flex: 0 0 25%;
                flex: 0 0 25%;
                -webkit-flex: 0 0 25%;
                max-width: 25%;
            }

            &:first-child:nth-last-child(4) ~ .footer-item {
                -webkit-box-flex: 0;
                -ms-flex: 0 0 25%;
                -webkit-flex: 0 0 25%;
                flex: 0 0 25%;
                max-width: 25%;
            }

            &:first-child:nth-last-child(5) {
                -webkit-box-flex: 0;
                -ms-flex: 0 0 20%;
                -webkit-flex: 0 0 20%;
                flex: 0 0 20%;
                max-width: 20%;
            }

            &:first-child:nth-last-child(5) ~ .footer-item {
                -webkit-box-flex: 0;
                -ms-flex: 0 0 20%;
                -webkit-flex: 0 0 20%;
                flex: 0 0 20%;
                max-width: 20%;
            }

            & h6 {
                display: block;
                width: 100%;
            }

            & ul {
                list-style: none;
                margin: 0;
                padding: 0;
            }
        }
    }

    & .footer-links {
        -webkit-box-flex: 0;
        -ms-flex: 0 0 100%;
        flex: 0 0 100%;
        -webkit-flex: 0 0 100%;
        max-width: 100%;
        display: flex;
        display: -webkit-flex;
        flex-wrap: wrap;
        -webkit-flex-wrap: wrap;
        padding-right: 15px;
        padding-left: 15px;

        & ul {
            list-style: none;
            margin: 0 auto;
            padding: 0;
            width: 100%;

            & li {
                display: inline-block;
                border-right: 1px solid #333;
                padding-right: 10px;
                padding-left: 5px;
                line-height: 12px;

                &:first-child {
                    padding-left: 0;
                }
            }
        }
    }

    & .footer-text {
        -webkit-box-flex: 0;
        -ms-flex: 0 0 100%;
        flex: 0 0 100%;
        -webkit-flex: 0 0 100%;
        max-width: 100%;
        display: flex;
        display: -webkit-flex;
        flex-wrap: wrap;
        -webkit-flex-wrap: wrap;
        padding-right: 15px;
        padding-left: 15px;
    }


    & .footer-copy-logo {
        -webkit-box-flex: 0;
        -ms-flex: 0 0 25%;
        flex: 0 0 25%;
        -webkit-flex: 0 0 25%;
        max-width: 25%;
        display: flex;
        display: -webkit-flex;
        flex-wrap: wrap;
        -webkit-flex-wrap: wrap;
        padding-left: 15px;
    }

    & .footer-copy-text {
        -webkit-box-flex: 0;
        -ms-flex: 0 0 75%;
        flex: 0 0 75%;
        -webkit-flex: 0 0 75%;
        max-width: 75%;
        display: flex;
        display: -webkit-flex;
        flex-wrap: wrap;
        -webkit-flex-wrap: wrap;
    }
}


