.multicolumn {
    position: relative;
    margin-bottom: $componentmargin-btm;


    & .item {
        padding: 0px 15px 0px 15px;
        display: flex;
        display: -webkit-flex;
        flex-wrap: wrap;
        -webkit-flex-wrap: wrap;
        width: 50%;

        &:only-child {
            -webkit-box-flex: 0;
            -ms-flex: 0 0 100%;
            flex: 0 0 100%;
            -webkit-flex: 0 0 100%;
            max-width: 100%;
        }

        &:first-child:nth-last-child(2) {
            width: 50%;
        }

        &:first-child:nth-last-child(2) ~ .item {
            width: 50%;
        }
        /* three items */
        &:first-child:nth-last-child(3) {
            width: 33.3333%;
        }

        &:first-child:nth-last-child(3) ~ .item {
            width: 33.3333%;
        }

        & .content-area {
            & h2 {
                font-size: $h2-fontSize;
            }
        }
    }
}


@media only screen and (max-width:$mobileMaxwidth) {
    .multicolumn {

        & .item {
            padding: 0px 0px 0px 0px;
            display: flex;
            display: -webkit-flex;
            flex-wrap: wrap;
            -webkit-flex-wrap: wrap;
            width: 100%;

            &:first-child:nth-last-child(2) {
                width: 100%;
            }

            &:first-child:nth-last-child(2) ~ .item {
                width: 100%;
            }
            /* three items */
            &:first-child:nth-last-child(3) {
                width: 100%;
            }

            &:first-child:nth-last-child(3) ~ .item {
                width: 100%;
            }

            & .content-area {
                & h2 {
                    font-size: $h2-fontSize-mobile;
                }
            }
        }
    }
}

@media only screen and (min-width:$tabMinwidth) and (max-width:$tabMaxwidth) {
    .multicolumn {


        & .item {
            padding: 0px 0px 0px 0px;
            display: flex;
            display: -webkit-flex;
            flex-wrap: wrap;
            -webkit-flex-wrap: wrap;
            width: 100%;

            &:first-child:nth-last-child(2) {
                width: 50%;
            }

            &:first-child:nth-last-child(2) ~ .item {
                width: 50%;
            }
            /* three items */
            &:first-child:nth-last-child(3) {
                width: 100%;
                padding-bottom: 40px;
            }

            &:first-child:nth-last-child(3) ~ .item {
                width: 100%;
                padding-bottom: 40px;
            }

            & .content-area {
                & h2 {
                    font-size: $h2-fontSize-tab;
                }
            }
        }
    }
}
