.backtotop {
    position: fixed;
    bottom: 20%;
    z-index: 1;
    right: 2%;
    display: none;
    cursor: pointer;
    .icon {
        width: 45px;
        img {
            width: 100%;
        }
    }
    .text {
        border-bottom: 2px solid #333;
        font-weight: bold;
    }
}