.cookie-section {
    background: #f1f1f1;
    position: fixed;
    z-index: 100;
    right: 0;
    bottom: 0;

    & .container {
        max-width: 100%;
    }

    & .cookie-text {
        width: 100%;
        padding: 20px;
    }

    & .btn-primary {
        float: right;
        text-decoration: none;
    }

    & a {
        text-decoration: underline;
        display: inline-block;
    }
}


